import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

export default new Vuex.Store({
	// (vuedata)
  state: {
			headerTitle:'',
			cityId:20,
			citynm:'',
  },
  //	(vuemethods)
  mutations: {
			changeHT(state,payload){
				//state   payload	()
				state.headerTitle = payload;
			},
			changeCI(state,payload){
				//state   payload	()
				state.cityId = payload.id;
				state.citynm = payload.nm;
			}
  },
  //
  actions: {

  }
})
