<template>
	<!---->
	<div class="playing" v-infinite-scroll="loadMore" infinite-scroll-disabled="loading">
		<molist v-if="movieList" :movieList="movieList" v-on:type="getType" type='playing'></molist>
	</div>
</template>

<script>
//
import { Indicator } from 'mint-ui';
//
import { MessageBox } from 'mint-ui';


export default {
  name: 'playing',
  created(){
  	//http://m.maoyan.com == /maoyan
  	// -- (webpackdevServer) -- 
  	this.axios.get('/ajax/movieOnInfoList',{
				params:{
					token: ''
				}
			}).then((res)=>{
				//ID
				this.moviesIds = res.data.movieIds;
				this.moviesIds.splice(0,12);
			this.loadMore(this.moviesIds);
				this.loading = false;//
			
			//
				this.movieList = res.data.movieList;
		});
		//
		this.$store.commit('changeHT','')
		//title
		var tit = document.getElementsByTagName('title')[0];
		tit.innerText="";
  },
  data(){
  	return{
  		moviesIds:[],//ID
  		movieList:[],//
			loading:true,//
  	}
  },
  methods:{
  		
  		//
  		getType(data){
  			this.$router.push({name:"movie",params:{
  				movieId:data
  			}})
  		},
  		
  		//
  		loadMore(){
  			if (this.movieList.length == 0) {
  				return false;
  			}
  			this.loading = true;
  			let a = this.moviesIds.splice(0,12);
  			if (a.length != 0) {
  			  Indicator.open('...');
			  setTimeout(() => {
				this.getmovies(a.join(','));//
			  }, 1000);
  			}else{
  				MessageBox.alert('','');
  			}
  		},
  		
  		//
  		getmovies(a){
            this.axios.get('/ajax/moreComingList' ,{
			    params: {
			      	token:'',
					movieIds: a
			    }
			  }
          ).then((res)=>{
           		this.movieList = this.movieList.concat(res.data.coming);//
			    Indicator.close();
                this.loading = false;
            })
       },
	},
}
</script>

